.ActionsList {
  padding: 0px 10px;
  margin-top: 10px;
}
.ActionsList p {
  text-align: center;
  font-size: 12px;
}

.actionElement {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  color: var(--main-elements-color);
  font-weight: 400;
  padding: 4px 0px;
  box-sizing: border-box;
}
.actionElement h5 {
  font-size: 13px;
}
.actionElement h6 {
  font-size: 11px;
}
.actionElement:not(:last-child) {
  border-bottom: 1px solid var(--alpha6);
}/*# sourceMappingURL=ActionsList.css.map */