.HabitFromLibrary {
	margin-top: 10px;
	padding: 10px 10px;
	border-radius: 10px;
	background-color: var(--main-bg-color);
	display: flex;
	justify-content: space-between;
	gap: 5px;
	.habit-title {
		word-break: break-all;
	}
	&-manageMenu {
		display: flex;
		align-items: center;
		button {
			padding: 15px 5px;
			background-color: var(--blue-main-color);
			border-radius: 10px;
			box-sizing: border-box;
			height: 90%;
			opacity: 0.6;
			transition: opacity 0.2s ease;

			svg {
				width: 20px;
				stroke: #fff;
			}
			&:hover {
				opacity: 1;
			}
			&:disabled {
				opacity: 0.4;
				cursor: auto;
				background-color: var(--green-color-2);
			}
		}
	}
}
