.LoaderHorizontally {
  display: flex;
  align-items: center;
  height: 30px;
  width: 40px;
  margin: auto;
  margin-top: 10px;
}
.LoaderHorizontally .item-1 {
  animation-delay: 0.2s;
  -webkit-animation-delay: 0.2s;
}
.LoaderHorizontally .item-2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s;
}
.LoaderHorizontally .item-3 {
  animation-delay: -1.2s;
  -webkit-animation-delay: -1.2s;
}

.LoaderHorizontally__item {
  content: "";
  background-color: var(--main-elements-color);
  display: block;
  margin: 0px 4.5px;
  border-radius: 2px;
  width: 5px;
  animation: loaderH 3s infinite linear;
  opacity: 0.8;
}

@keyframes loaderH {
  0% {
    height: 5px;
  }
  50% {
    height: 100%;
  }
  100% {
    height: 5px;
  }
}/*# sourceMappingURL=LoaderHorizontally.css.map */