.AddHabitsForm {
	width: 100%;
	border-radius: 10px;
	margin-top: 10px;
	box-sizing: border-box;
	background-color: var(--main-bg-color);
	&-title {
		font-size: 16px;
		font-weight: 400;
	}
	&-header {
		padding: 15px;
		display: flex;
		justify-content: space-between;
		gap: 10px;
		width: 100%;
		align-items: center;
	}
	.openArrow {
		height: 15px;
		svg {
			padding: 3px 5px;
		}
		&.active {
			transform: rotateX(180deg);
		}
	}

	&-content {
		padding: 0px 15px 10px 15px;
		margin-top: 5px;
		display: none;
		&.active {
			display: block;
		}
	}
	.description {
		font-size: 12px;
		text-align: center;
		font-weight: 400;
	}

	.text-input,
	select {
		margin-top: 10px;
		width: 100%;
		box-sizing: border-box;
		padding: 6px 10px;
		border-radius: 5px;
		background-color: var(--alternative-bg-color);
		border: 1px solid var(--alpha3);

		&.small-input {
			flex: 1 1;
			max-width: 50%;
			min-width: 190px;
			margin: 0;
		}
	}
	#AddHabitsForm-fileInput {
		display: none;
	}
	.fileInput {
		cursor: pointer;
		text-align: center;
		display: block;
		box-sizing: border-box;
		margin-top: 15px;
		width: 100%;
		padding: 4px 10px;
		border-radius: 5px;
		background-color: var(--alternative-bg-color);
		border: 1px dashed var(--alpha3);
	}
	.submit-btn {
		margin: auto;
		margin-top: 15px;
		&:enabled {
			background-color: var(--blue-main-color);
		}
	}
	.secondaryInputs-box {
		margin-top: 10px;
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
	}
}
