.Settings {
  padding: 10px;
  border-radius: 10px;
  background-color: var(--main-bg-color);
  margin-top: 15px;
}
.Settings input {
  margin-top: 10px;
  width: 100%;
  border: 1px solid var(--alpha6);
  border-radius: 5px;
  padding: 5px;
  box-sizing: border-box;
  background-color: var(--second-bg-color);
}
.Settings button {
  background-color: var(--blue-main-color);
  padding: 5px 10px;
  border-radius: 10px;
  margin-top: 10px;
  color: #fff;
