.UserEventsLayout {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: var(--main-bg-color);
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;

	&-item {
		margin-top: 10px;
	}
	.close-btn {
		margin-left: auto;
		margin-top: 10px;
		padding: 5px;
		opacity: 0.7;
		transition: opacity 0.2s ease;

		svg {
			stroke: var(--alpha-main);
			width: 20px;
			height: 20px;
		}
		&:hover {
			opacity: 1;
		}
	}
	.image-box {
		display: flex;
		justify-content: center;
		svg {
			max-width: 250px;
			max-height: 250px;
			width: 100%;
		}
	}
	.content-box {
		margin-top: 15px;

		.event-title {
			font-size: 20px;
			text-align: center;
			font-weight: 700;
		}

		.manage-btns-box {
			display: flex;
			justify-content: center;
			gap: 15px;
			margin-top: 15px;

			button {
				padding: 10px 15px;
				border-radius: 10px;
				color: #fff;
				opacity: 0.8;
				transition: opacity 0.2s ease;
				font-size: 14px;
				font-weight: 700;

				&:hover {
					opacity: 1;
				}
			}
			.manage-btns_close {
				background-color: var(--grey-color-2);
			}
		}
	}
	.CoinsView {
		margin-top: 10px;
	}
}

.BuyHealingPotion {
	.manage-btns_buy {
		background-color: #ef4545;
	}
}

.NullHealth {
	.image-box {
		svg {
			aspect-ratio: calc(28 / 22);
			height: auto;
			fill: #ef4545;
		}
		span {
			font-size: 15vw;
		}
	}
	.manage-btns_pay {
		background-color: #ef4545;
	}
}

.LevelUp .content-box .manage-btns-box button.manage-btns_close {
	background-color: var(--blue-main-color);
}

.OpenChest .image-box img {
	max-width: 350px;
	width: 100%;
}
.OpenChest .manage-btns_continue {
	background-color: var(--blue-main-color);
}
