.DaysActivityView {
  display: flex;
  gap: 3px;
  align-items: center;
}
.DaysActivityView-count {
  font-size: 17px;
  font-weight: 700;
  color: var(--grey-color-1);
}
.DaysActivityView svg {
  width: 22px;
}
.DaysActivityView .bigPath {
  fill: var(--grey-color-1);
}
.DaysActivityView .smallPath {
  fill: var(--main-bg-color);
}
.DaysActivityView.active .bigPath {
  fill: var(--orange-main-color);
}
.DaysActivityView.active .smallPath {
  fill: #ffc800;
}
.DaysActivityView.active .DaysActivityView-count {
  color: var(--orange-main-color);
}/*# sourceMappingURL=DaysActivityView.css.map */