.ActionsList {
	padding: 0px 10px;
	margin-top: 10px;
	p {
		text-align: center;
		font-size: 12px;
	}
}

.actionElement {
	display: flex;
	justify-content: space-between;
	gap: 10px;
	color: var(--main-elements-color);
	font-weight: 400;
	padding: 4px 0px;
	box-sizing: border-box;
	h5 {
		font-size: 13px;
	}
	h6 {
		font-size: 11px;
	}
	&:not(:last-child) {
		border-bottom: 1px solid var(--alpha6);
	}
}
