.AddHabitsForm {
  width: 100%;
  border-radius: 10px;
  margin-top: 10px;
  box-sizing: border-box;
  background-color: var(--main-bg-color);
}
.AddHabitsForm-title {
  font-size: 16px;
  font-weight: 400;
}
.AddHabitsForm-header {
  padding: 15px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  width: 100%;
  align-items: center;
}
.AddHabitsForm .openArrow {
  height: 15px;
}
.AddHabitsForm .openArrow svg {
  padding: 3px 5px;
}
.AddHabitsForm .openArrow.active {
  transform: rotateX(180deg);
}
.AddHabitsForm-content {
  padding: 0px 15px 10px 15px;
  margin-top: 5px;
  display: none;
}
.AddHabitsForm-content.active {
  display: block;
}
.AddHabitsForm .description {
  font-size: 12px;
  text-align: center;
  font-weight: 400;
}
.AddHabitsForm .text-input,
.AddHabitsForm select {
  margin-top: 10px;
  width: 100%;
  box-sizing: border-box;
  padding: 6px 10px;
  border-radius: 5px;
  background-color: var(--alternative-bg-color);
  border: 1px solid var(--alpha3);
}
.AddHabitsForm .text-input.small-input,
.AddHabitsForm select.small-input {
  flex: 1 1;
  max-width: 50%;
  min-width: 190px;
  margin: 0;
}
.AddHabitsForm #AddHabitsForm-fileInput {
  display: none;
}
.AddHabitsForm .fileInput {
  cursor: pointer;
  text-align: center;
  display: block;
  box-sizing: border-box;
  margin-top: 15px;
  width: 100%;
  padding: 4px 10px;
  border-radius: 5px;
  background-color: var(--alternative-bg-color);
  border: 1px dashed var(--alpha3);
}
.AddHabitsForm .submit-btn {
  margin: auto;
  margin-top: 15px;
}
.AddHabitsForm .submit-btn:enabled {
  background-color: var(--blue-main-color);
}
.AddHabitsForm .secondaryInputs-box {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}/*# sourceMappingURL=AddHabitsForm.css.map */