.LoaderWindow {
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	z-index: 150;
	background-color: var(--main-bg-color);
	display: flex;
	align-items: center;
	justify-content: center;

	&-content {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		gap: 10px;
		font-weight: 700;
		font-size: 18px;
	}
}
