.header {
  width: 100%;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.5);
  position: fixed;
  background-color: var(--main-bg-color);
  top: 0;
  z-index: 50;
}
.header .container {
  position: relative;
}
.header-content {
  padding: 6px 0px;
  background-color: var(--main-bg-color);
  position: relative;
  z-index: 50;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
}
.header .level-bar {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  gap: 5px;
  color: var(--blue-main-color);
}
.header .level-bar .level {
  min-width: 31px;
  text-align: center;
}
.header .level-bar .line-experience {
  flex: 1 1 auto;
  height: 10px;
  border-radius: 5px;
  background-color: var(--alternative-bg-color);
}
.header .level-bar .line-experience .progres {
  position: relative;
  transition: width 0.5s linear;
  display: block;
  height: 100%;
  content: "";
  min-width: 10px;
  border-radius: 5px;
  background-color: var(--blue-main-color);
}
.header .userMenu-openBtn {
  height: 34px;
  width: 34px;
}
.header .userMenu-openBtn img {
  box-sizing: border-box;
  border: 2px solid var(--alpha3);
  border-radius: 50%;
  height: inherit;
  width: inherit;
}
.header .computerUserStates {
  display: flex;
  gap: 10px;
}
@media (max-width: 680px) {
  .header .computerUserStates {
    display: none;
  }
}

#holeInGreyIcon {
  fill: var(--main-bg-color);
}

#mainInGreyIcon {
  fill: var(--grey-color-1);
}/*# sourceMappingURL=Header.css.map */