@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap");

* {
	margin: 0;
	padding: 0;
	text-decoration: none;
	outline: none;
	font-family: "Roboto", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}
button {
	background: none;
	border: none;
	cursor: pointer;
	display: block;
}
body {
	color: var(--main-elements-color);
}
body.modalActive {
	overflow: hidden;
}

:root {
	--main-bg-color: #fff;
	--body-bg-color: #d9d9d9;
	--alternative-bg-color: #ebe6e6;
	--alternative-bg-color--hover: rgba(174, 183, 194, 0.12);
	--second-bg-color: rgb(222, 222, 222);
	--main-elements-color: rgb(0, 0, 0);
	--alpha-main: rgba(0, 0, 0, 0.8);
	--alpha9: rgba(0, 0, 0, 0.9);
	--alpha8: rgba(0, 0, 0, 0.8);
	--alpha7: rgba(0, 0, 0, 0.7);
	--alpha6: rgba(0, 0, 0, 0.6);
	--alpha5: rgba(0, 0, 0, 0.5);
	--alpha3: rgba(0, 0, 0, 0.3);
	--alpha2: rgba(0, 0, 0, 0.2);
	--alpha1: rgba(0, 0, 0, 0.1);
	--delta-main: rgba(255, 255, 255, 1);
	--delta7: rgba(255, 255, 255, 0.7);
	--delta5: rgba(255, 255, 255, 0.5);
	--grey-color-1: #cccbcb;
	--grey-color-2: rgb(146, 146, 146);
	--carefully-color-1: #f70000;
	--cancel-btn: var(--alpha5);
	--cancel-btn-hover: var(--alpha7);
	--green-color: #d2e3c8;
	--green-color-2: #58cc02;
	--blue-main-color: #00aaec;
	--blue-main-color-hover: #2664ac;
	--orange-main-color: #ff9600;

	--basic-padding-horizontally: 15px;
	--basic-padding-horizontally-wide: 20px;
}

@media (max-width: 800px) {
	:root {
		--basic-padding-horizontally: 5px;
		--basic-padding-horizontally-wide: 15px;
	}
}
