.filterStatus-item {
  padding: 5px 10px;
  color: var(--main-elements-color);
  background-color: var(--main-bg-color);
  transition: background-color 0.2s linear;
  border-radius: 10px;
  font-size: 16px;
  box-sizing: border-box;
  white-space: nowrap;
}
.filterStatus-item:hover {
  background-color: var(--alternative-bg-color);
}
.filterStatus-item.active {
  background-color: var(--second-bg-color);
  border: 1px solid var(--alpha2);
}
.filterStatus-item:disabled {
  cursor: auto;
}/*# sourceMappingURL=SetFilterBtn.css.map */