.UserMenu {
	position: absolute;
	max-width: 300px;
	border-radius: 0px 0px 10px 10px;
	right: 10px;
	top: 100%;
	width: 100%;
	background-color: var(--main-bg-color);
	padding: 10px 0px 20px 0px;
	box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2);
	border: 2px solid var(--alpha3);
	border-top: none;
	box-sizing: border-box;
	overflow-y: auto;

	&-userStates {
		display: none;
		align-items: center;
		gap: 5px;
		justify-content: space-around;
		@media (max-width: 680px) {
			& {
				display: flex;
			}
		}
	}
	.HealthView svg {
		width: 28px;
	}
	.navigate {
		margin-top: 5px;
		&-item {
			display: flex;
			align-items: center;
			gap: 10px;
			padding: 5px 15px;
			font-size: 18px;
			color: var(--main-elements-color);
			text-decoration: none;
			&:hover {
				background-color: var(--alternative-bg-color);
			}

			.icon-box {
				height: 28px;
				max-width: 31px;
				width: 31px;
				svg {
					display: block;
					margin: auto;
					height: 100%;
					max-width: inherit;
				}
			}
		}
	}
	@media (max-width: 680px) {
		& {
			z-index: 10;
			top: 0;
			padding-top: 46px;
			right: 0;
			border: none;
			border-radius: 0px;
			height: 100vh;
			max-width: 100%;
			box-shadow: none;

			.navigate {
				margin-top: 15px;
				display: flex;
				flex-direction: column;
				gap: 10px;
				justify-content: center;
				align-items: stretch;
				&-item {
					justify-content: center;
				}
			}
		}
	}
}
