.container {
	padding: 0px var(--basic-padding-horizontally);
	max-width: 1200px;
	margin: 0px auto;
	box-sizing: border-box;
}
body {
	background-color: var(--body-bg-color);
	overflow-x: hidden;
	width: 100%;
	height: 100vh;
}

.layout-main {
	padding-top: 50px;
}

.error {
	color: var(--carefully-color-1);
	text-align: center;
	margin-top: 5px;
	font-size: 14px;
	font-weight: 700;
}

.commonButton {
	border-radius: 10px;
	display: block;
	text-align: center;
	padding: 7px 16px;
	font-size: 13px;
	font-weight: 700;
	color: var(--main-elements-color);
	transition: opacity 200ms linear;
	background-color: var(--cancel-btn);
	color: #fff;
	letter-spacing: 0.5px;
	opacity: 0.8;
}
.commonButton:disabled {
	cursor: auto;
}
@media (hover: hover) {
	.commonButton:enabled:hover {
		opacity: 1;
	}
}
@media (hover: none) {
	.commonButton:enabled:active {
		opacity: 1;
	}
}
