.DaysActivitySchedule {
	margin-top: 15px;
	padding: 15px 10px 20px 10px;
	background-color: var(--main-bg-color);
	border-radius: 10px;
	color: var(--main-elements-color);
	text-align: center;
	&-title {
		font-size: 20px;
		font-weight: 700;
	}
	&-description {
		font-weight: 400;
		font-size: 14px;
	}
}

.DaysActivitySchedule-schedule {
	display: flex;
	align-items: flex-end;
	gap: 10px;
	max-height: 300px;
	height: 300px;
	border-left: 1px solid #000;
	border-bottom: 1px solid #000;
	padding: 10px;
	box-sizing: border-box;
	overflow: auto;

	.schedule-column {
		display: flex;
		justify-content: center;
		align-items: center;
		flex: 1 1;
		font-size: 14px;
		font-weight: 400;
		border-radius: 5px;
		writing-mode: vertical-lr;
		min-width: 30px;
		text-align: center;
		min-height: 5px;
		cursor: auto;
		background-color: var(--blue-main-color);
		color: #fff;
		overflow: hidden;
		word-break: keep-all;
		&:hover {
			background-color: var(--blue-main-color-hover);
		}
	}

	@media (max-width: 600px) {
		& {
			gap: 5px;
		}
	}
}
