.registration {
  display: flex;
  align-items: center;
  height: 100vh;
}

.registration main {
  background-color: var(--main-bg-color);
  max-width: 600px;
  flex: 1 1 auto;
  margin: auto;
  padding: 40px 15px;
  border-radius: 10px;
}
.registration main .title-box {
  text-align: center;
  font-weight: 700;
  font-size: 25px;
}
.registration main .input-box {
  margin-top: 10px;
}
.registration main .input-box .input-placeholde {
  margin-bottom: 2px;
  font-weight: 400;
}
.registration main .input-box input {
  padding: 2px 5px;
  width: 100%;
  box-sizing: border-box;
  border: 2px dashed var(--blue-main-color);
  border-radius: 5px;
  background-color: var(--alternative-bg-color);
}
.registration main .submit-btn-box {
  margin-top: 10px;
}
.registration main .submit-btn-box button {
  margin: auto;
}
.registration main .submit-btn-box button:enabled {
  background-color: var(--blue-main-color);
}/*# sourceMappingURL=Registration.css.map */