#addActionsForm {
  margin-top: 5px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  align-items: stretch;
  border: 1px solid var(--alpha5);
  border-radius: 5px;
}
#addActionsForm input {
  flex: 1 1 auto;
  background-color: var(--second-bg-color);
  border-radius: 5px 0px 0px 5px;
  padding: 3px 5px;
  border: none;
}
#addActionsForm button {
  background-color: var(--green-color-2);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0px 5px 5px 0px;
  padding: 0px 8px;
  opacity: 0.7;
  transition: opacity 0.2s ease;
}
#addActionsForm button svg {
  stroke: var(--main-elements-color);
  width: 10px;
}
#addActionsForm button:enabled:hover {
  opacity: 1;
}/*# sourceMappingURL=AddActionForm.css.map */