.HabitFromLibrary {
  margin-top: 10px;
  padding: 10px 10px;
  border-radius: 10px;
  background-color: var(--main-bg-color);
  display: flex;
  justify-content: space-between;
  gap: 5px;
}
.HabitFromLibrary .habit-title {
  word-break: break-all;
}
.HabitFromLibrary-manageMenu {
  display: flex;
  align-items: center;
}
.HabitFromLibrary-manageMenu button {
  padding: 15px 5px;
  background-color: var(--blue-main-color);
  border-radius: 10px;
  box-sizing: border-box;
  height: 90%;
  opacity: 0.6;
  transition: opacity 0.2s ease;
}
.HabitFromLibrary-manageMenu button svg {
  width: 20px;
  stroke: #fff;
}
.HabitFromLibrary-manageMenu button:hover {
  opacity: 1;
}
.HabitFromLibrary-manageMenu button:disabled {
  opacity: 0.4;
  cursor: auto;
  background-color: var(--green-color-2);
}/*# sourceMappingURL=HabitFromLibrary.css.map */