.selectCategory {
	background-color: var(--main-bg-color);
	border-radius: 10px;
	padding: 15px 10px 5px 10px;
	margin-top: 10px;

	&-title {
		text-align: center;
		font-weight: 700;
		font-size: 18px;
	}
	&-categories {
		display: flex;
		gap: 10px;
		margin-top: 10px;
		overflow-x: auto;
		padding: 0px 10px 10px 10px;
	}
}
.Library-habits {
	padding-bottom: 15px;
}
