.DaysActivityView {
	display: flex;
	gap: 3px;
	align-items: center;

	&-count {
		font-size: 17px;
		font-weight: 700;
		color: var(--grey-color-1);
	}
	svg {
		width: 22px;
	}
	.bigPath {
		fill: var(--grey-color-1);
	}
	.smallPath {
		fill: var(--main-bg-color);
	}

	&.active {
		.bigPath {
			fill: var(--orange-main-color);
		}
		.smallPath {
			fill: #ffc800;
		}
		.DaysActivityView-count {
			color: var(--orange-main-color);
		}
	}
}
