.Shop {
	margin-top: 10px;
	box-sizing: border-box;
	width: 100%;
	border-radius: 10px;
	padding: 15px;
	background-color: var(--main-bg-color);

	&-title {
		text-align: center;
		font-size: 22px;
		font-weight: 700;
	}
	&-products {
		margin-top: 10px;
	}
	.products-item {
		padding: 15px 0px;
		border-top: 2px solid var(--alpha3);
		display: flex;
		justify-content: start;
		align-items: center;
		gap: 10px;
	}
	.product-image {
		max-height: 130px;
		height: 130px;
		max-width: 200px;
		width: 100%;
		svg {
			display: block;
			margin: auto;
			max-height: inherit;
			height: 100%;
		}
	}
	.product-content {
		.description-title {
			font-size: 18px;
			font-weight: 700;
		}
		p {
			font-size: 14px;
			margin-top: 5px;
		}
	}

	.product-buyBtn {
		margin-top: 10px;
	}
	.product-buyBtn button {
		padding: 3px 10px;
		font-size: 14px;
		box-sizing: border-box;
		border: 3px solid var(--blue-main-color);
		border-radius: 15px;
		color: var(--main-elements-color);
		transition: background-color 0.2s ease;

		&:disabled {
			border: 3px solid var(--grey-color-2);
			cursor: auto;
		}
		&:enabled:hover {
			background-color: var(--alpha1);
		}
	}
	@media (max-width: 650px) {
		.products-item {
			display: block;
			text-align: center;
		}
		.product-image {
			margin: auto;
		}
		.product-content {
			margin-top: 10px;
		}
		.product-buyBtn {
			display: flex;
			justify-content: center;
		}
	}
}
