.Shop {
  margin-top: 10px;
  box-sizing: border-box;
  width: 100%;
  border-radius: 10px;
  padding: 15px;
  background-color: var(--main-bg-color);
}
.Shop-title {
  text-align: center;
  font-size: 22px;
  font-weight: 700;
}
.Shop-products {
  margin-top: 10px;
}
.Shop .products-item {
  padding: 15px 0px;
  border-top: 2px solid var(--alpha3);
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 10px;
}
.Shop .product-image {
  max-height: 130px;
  height: 130px;
  max-width: 200px;
  width: 100%;
}
.Shop .product-image svg {
  display: block;
  margin: auto;
  max-height: inherit;
  height: 100%;
}
.Shop .product-content .description-title {
  font-size: 18px;
  font-weight: 700;
}
.Shop .product-content p {
  font-size: 14px;
  margin-top: 5px;
}
.Shop .product-buyBtn {
  margin-top: 10px;
}
.Shop .product-buyBtn button {
  padding: 3px 10px;
  font-size: 14px;
  box-sizing: border-box;
  border: 3px solid var(--blue-main-color);
  border-radius: 15px;
  color: var(--main-elements-color);
  transition: background-color 0.2s ease;
}
.Shop .product-buyBtn button:disabled {
  border: 3px solid var(--grey-color-2);
  cursor: auto;
}
.Shop .product-buyBtn button:enabled:hover {
  background-color: var(--alpha1);
}
@media (max-width: 650px) {
  .Shop .products-item {
    display: block;
    text-align: center;
  }
  .Shop .product-image {
    margin: auto;
  }
  .Shop .product-content {
    margin-top: 10px;
  }
  .Shop .product-buyBtn {
    display: flex;
    justify-content: center;
  }
}/*# sourceMappingURL=Shop.css.map */