.HabitElement {
  margin-top: 10px;
  padding: 10px 15px;
  border-radius: 10px;
  background-color: var(--main-bg-color);
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
.HabitElement .habit-title {
  word-break: break-all;
}
.HabitElement.complited {
  padding: 7px 12px;
  border: 3px dashed var(--green-color-2);
}
.HabitElement.notComplited {
  padding: 7px 12px;
  border: 3px dashed var(--carefully-color-1);
}
.HabitElement-header {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  flex-wrap: nowrap;
  align-items: start;
}
.HabitElement-manageMenu {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 10px;
  align-items: stretch;
}
.HabitElement-manageMenu-item {
  height: 29px;
  display: flex;
  align-items: center;
  padding: 6px 7px;
  border-radius: 5px;
  color: #fff;
  opacity: 0.6;
  transition: opacity 0.2s ease;
  font-size: 13px;
}
.HabitElement-manageMenu-item svg {
  stroke: #fff;
  width: 15px;
}
.HabitElement-manageMenu-item.complite-btn {
  background-color: var(--green-color-2);
}
.HabitElement-manageMenu-item.refuse-btn {
  background-color: var(--carefully-color-1);
}
.HabitElement-manageMenu-item.addAction-btn {
  background-color: var(--blue-main-color);
}
.HabitElement-manageMenu-item.tryAgain-btn {
  background-color: var(--blue-main-color);
}
.HabitElement-manageMenu-item.remove-btn {
  background-color: var(--carefully-color-1);
}
.HabitElement-manageMenu-item:hover {
  opacity: 1;
}
@media (max-width: 680px) {
  .HabitElement {
    display: block;
  }
  .HabitElement-manageMenu {
    gap: 15px;
    flex-direction: row;
    justify-content: flex-end;
  }
}

.habit-info {
  display: flex;
  gap: 5px;
  color: var(--main-elements-color);
  font-size: 14px;
  align-items: center;
  margin-top: 2px;
}
.habit-info svg {
  stroke: var(--orange-main-color);
  min-width: 15px;
  width: 15px;
}/*# sourceMappingURL=HabitElement.css.map */